
* {
  box-sizing: border-box;
}

html, #root {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;

  background: #1B1F27;

  background: var(--dark-gray-d);

  height: 100%;
  width: 100%;
}

:root {
  --cyan: #61dafb;
  --cyan-transparent: #61dafb33;

  --purple: #9985FB;
  --purple-transparent: #9985FB33;
  --purple-h: #b39fff;

  --green: #6EFB7A;

  --dark-gray: #282c34;
  --dark-gray-h: #42464E;
  --dark-gray-d: #1B1F27;

  --gray: #757981;
}

body.light {
}

body.dark {
  --fontcolor: var(--cyan);
  --highlight-font: var(--purple);

  --background: var(--dark-gray-d);
  --primary: var(--dark-gray);
  --secondary: var(--dark-gray-h);
}

.navbar {  
  width: 100%;
  height: 70px;
  
  background: #282c34;
  
  background: var(--dark-gray);
  color: #9985FB;
  color: var(--purple);
  border-bottom: 5px solid #42464E;
  border-bottom: 5px solid var(--dark-gray-h);

  display: -webkit-flex;

  display: flex;
  /* justify-content: center; */
  -webkit-align-items: center;
          align-items: center;
}

.navbar .title {
  justify-self: left;

  font-weight: bold;
  margin: 0 30px;
  font-size: 32px;

  font-family: "Lobster";
}

.navbar .link {
  box-sizing: content-box;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;

  padding: 0 15px;
  height: 100%;

  margin-bottom: -5px;

  border-bottom: 5px solid #42464E;

  border-bottom: 5px solid var(--dark-gray-h);

  color: #61dafb;

  color: var(--cyan);
  font-weight: bold;
  font-size: 22px;

  text-decoration: none;

  transition: border-bottom-color 0.4s;
}

.navbar .link:hover {
  border-bottom-color: #61dafb;
  border-bottom-color: var(--cyan);

}

.navbar .link.selected {
  color: #9985FB;
  color: var(--purple);
  border-bottom-color: #9985FB;
  border-bottom-color: var(--purple);

  background: #42464E;

  background: var(--dark-gray-h);
}

.footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;

  width: 100%;
  height: 30px;
  background: #282c34;
  background: var(--dark-gray);
  color: #757981;
  color: var(--gray);

  border-top: 1px solid #42464E;

  border-top: 1px solid var(--dark-gray-h);
}


.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  height: 100%;
  width: 100%;

  -webkit-flex-direction: column;

          flex-direction: column;
  -webkit-justify-content: stretch;
          justify-content: stretch;
  -webkit-align-items: center;
          align-items: center;

}

.contentWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;

  -webkit-flex: 1 1;

          flex: 1 1;
  width: 100%;

  -webkit-align-items: flex-start;

          align-items: flex-start;
  -webkit-justify-content: center;
          justify-content: center;

  padding: 20px 100px;

  overflow-y: auto;
}

.panel {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;

  -webkit-flex-basis: 200px;

          flex-basis: 200px;
  -webkit-flex-grow: 1;
          flex-grow: 1;

  padding: 10px 25px;
  margin: 20px;
  
  background: #282c34;
  
  background: var(--dark-gray);
  border: 1px solid #42464E;
  border: 1px solid var(--dark-gray-h);

  box-shadow: 0 0 30px 5px #61dafb33;

  box-shadow: 0 0 30px 5px var(--cyan-transparent);
}

.panel .text {
  text-align: left;
}

.panel .panel-section {
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-basis: 300px;
          flex-basis: 300px;

  padding: 15px;
}

.card {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-justify-content: stretch;
          justify-content: stretch;

  width: 400px;
  height: 325px;

  margin: 20px;
  
  background: #282c34;
  
  background: var(--dark-gray);
  border: 1px solid #42464E;
  border: 1px solid var(--dark-gray-h);

  box-shadow: 0 0 30px 5px #61dafb33;

  box-shadow: 0 0 30px 5px var(--cyan-transparent);
}

.card .title {
  border: 1px solid #757981;
  border: 1px solid var(--gray);
  border-bottom-width: 0;

  border-top-width: 4px;
  border-top-color: #9985FB;
  border-top-color: var(--purple);

  padding: 10px;
  text-align: center;
  background: #42464E;
  background: var(--dark-gray-h);
}

.card:hover {
  cursor: zoom-in;
}

.card:hover .title {
  background: #282c34;
  background: var(--dark-gray);

  border-top-color: #61dafb;

  border-top-color: var(--cyan);
}

.card .title .caption {
  color: #e0e0e0;
}

.card .image {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;

  -webkit-flex: 1 1;

          flex: 1 1;
  -webkit-justify-content: center;
          justify-content: center;
  
  border: 1px solid #757981;
  
  border: 1px solid var(--gray);
  border-width: 0 1px;
  
  overflow: hidden;
}

.card .links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;

  padding: 8px 5px 5px;

  border: 1px solid #757981;

  border: 1px solid var(--gray);
  border-top-width: 0;

  text-align: center;
  background: #42464E;
  background: var(--dark-gray-h);
}

.modal-overlay {
  position: absolute;
  display: -webkit-flex;
  display: flex;

  height: 100%;
  width: 100%;

  background: #00000066;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  opacity: 0;

  transition: opacity ease-out 0.5s;
}

.modal {
  position: absolute;
  width: 90%;
  max-width: 1000px;

  height: 90%;
  max-height: 700px;

  overflow: auto;

  background: #282c34;

  background: var(--dark-gray);
  border: 1px solid #42464E;
  border: 1px solid var(--dark-gray-h);
  box-shadow: 0 0 45px 5px #000000aa;

  padding: 15px 45px;
}

.modal .closeButton {
  position: absolute;
  cursor: pointer;

  right: 15px;
  top: 10px;

  color: #9985FB;

  color: var(--purple);
}

.modal .closeButton:hover {
  color: #61dafb;
  color: var(--cyan);
}

.project-modal-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

  -webkit-justify-content: stretch;

          justify-content: stretch;
  -webkit-align-items: center;
          align-items: center;
}

/* width */
::-webkit-scrollbar {
  width: 15px;
  cursor: pointer;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #9985FB;
  box-shadow: inset 0 0 5px var(--purple);
  background: #1B1F27;
  background: var(--dark-gray-d);
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #9985FB;
  background: var(--purple); 
  box-shadow: inset 0 0 10px #42464E; 
  box-shadow: inset 0 0 10px var(--dark-gray-h); 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b39fff;
  background: var(--purple-h); 
}
.Typography {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.Typography.default {
  color: #e0e0e0;
}

.Typography.special {
  font-family: 'Lobster';
  font-size: 32px;
}

.Typography.heading {
  font-size: 36px;
  font-weight: bold;

  color: var(--purple);
  margin: 16px 0 8px;
}

.Typography.subheading {
  font-size: 32px;

  color: var(--cyan);
  margin: 8px 0 4px;
}

.Typography.subsubheading {
  font-size: 26px;

  margin: 4px 0 2px;
  color: white;
}

.Typography.link {
  font-size: 18px;
  color: #a0a0a0;

  display: inline-block;

  cursor: pointer;
  text-decoration: underline;
}

.Typography.link:hover {
  color: var(--cyan);
}

.Typography.link i {
  color: var(--cyan);
}

.Typography.caption {
  font-size: 18px;
  color: #757981;
}

.Typography.subcaption {
  font-size: 16px;
  color: #5C6068;
}

.Typography.publication {
  font-size: 18px;
  color: #c0c0c0;

  margin: 8px 0 4px;
}

.Typography.publication strong {
  color: #e0e0e0;
}

.Typography.publication {
  font-size: 18px;
  color: #c0c0c0;

  margin: 8px 0 4px;
}

.Typography.award {
  font-size: 14px;
  color: var(--cyan);
}

.Typography.award-large {
  font-size: 20px;
  color: var(--cyan);
}

.Typography.default {
  font-size: 22px;
  text-align: justify;
}

.Typography.default:not(:last-of-type) {
  margin-bottom: 16px;
}

.Typography.default:not(:first-of-type) {
  margin-top: 8px;
}

.Typography.default strong,
.Typography.subsubheading strong {
  color: var(--purple);
}
